import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

// 添加错误处理的路由加载函数
const loadView = (view) => {
    return () => import(/* webpackChunkName: "view-[request]" */ `@/views/${view}.vue`)
        .catch(() => import('@/views/Error404.vue')) // 备用组件
}
// 主框架路由
const mainRoutes = {
    path: '/',
    component: loadView('index'), // 主框架组件
    children: [
        {
            path: '/',
            name: '系统首页',
            component: loadView('home'),
            meta: {icon: '', title: 'center', affix: true}
        },
        {
            path: '/updatePassword',
            name: '修改密码',
            component: loadView('update-password'),
            meta: {icon: '', title: 'updatePassword'}
        },
        {
            path: '/pay',
            name: '支付',
            component: loadView('pay'),
            meta: {icon: '', title: 'pay'}
        },
        {
            path: '/center',
            name: '个人信息',
            component: loadView('center'),
            meta: {icon: '', title: 'center'}
        },
        {
            path: '/news',
            name: '健康资讯',
            component: loadView('modules/news/list')
        },
        {
            path: '/fuwuzhipai',
            name: '服务指派',
            component: loadView('modules/fuwuzhipai/list')
        },
        {
            path: '/yonghu',
            name: '用户',
            component: loadView('modules/yonghu/list')
        },
        {
            path: '/yishengzixun',
            name: '医生咨询',
            component: loadView('modules/yishengzixun/list')
        },
        {
            path: '/shangmenshenqing',
            name: '上门申请',
            component: loadView('modules/shangmenshenqing/list')
        },
        {
            path: '/shequyisheng',
            name: '社区医生',
            component: loadView('modules/shequyisheng/list')
        },
        {
            path: '/keshi',
            name: '科室',
            component: loadView('modules/keshi/list')
        },
        {
            path: '/jiankangpingce',
            name: '健康评测',
            component: loadView('modules/jiankangpingce/list')
        },
        {
            path: '/tijianbaogao',
            name: '体检报告',
            component: loadView('modules/tijianbaogao/list')
        },
        {
            path: '/jiankangxinxi',
            name: '健康信息',
            component: loadView('modules/jiankangxinxi/list')
        },
        {
            path: '/newstype',
            name: '健康资讯分类',
            component: loadView('modules/newstype/list')
        }
    ]
}

export default new Router({
    mode: 'hash', // 可选：去除URL中的#
    base: process.env.BASE_URL,
    routes: [
        mainRoutes,
        {
            path: '/login',
            name: 'login', // 建议始终命名路由
            component: loadView('login'),
            meta: {icon: '', title: 'login'}
        },
        {
            path: '/register',
            name: 'register',
            component: loadView('register'),
            meta: {icon: '', title: 'register'}
        },
        // 404路由兜底（需先创建Error404.vue）
        {
            path: '*',
            component: loadView('Error404')
        }
    ]
})

// 保留原有的push方法处理
const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}